//     
export interface IUser {
    _id: string;
    email: string;
    name: string;
    surname: string;
    isAdmin: boolean;
}

//   
export interface ISignInResponse {
    email: string;
    password: string;
}

//   
export interface ISignUpResponse{
    name: string;
    surname: string;
    email: string;
    password: string;
    isAdmin: boolean;
}

//    
export interface IUpdateResponse {
    id: string;
    name: string;
    surname: string;
    email: string;
}

//  
export interface ITokens {
    accessToken: string;
    refreshToken: string;
}

//  state
export interface IInitialState {
    user: IUser | null;
    isLoading: boolean;
}

// 
export interface IAuthResponse extends ITokens {
    user: IUser;
}
//  
export enum Tokens {
    refreshToken = "refresh-token",
    accessToken = "access-token"
}
