const REQUIRED_FIELD = "  ";
export const emailValidation = {
    required: REQUIRED_FIELD,
    validate: (value: string) => {
        if (value.match(/[--]/g)) {
            return "   ";
        }
        if (
            !value.match(
                /^(\S+)@([a-z0-9-]+)(\.)([a-z]{2,4})(\.?)([a-z]{0,4})+$/g
            )
        ) {
            return " ";
        }
        return true;
    }
};

export const passwordValidation = {
    required: REQUIRED_FIELD,
    validate: (value: string) => {
        if (value.length < 6) {
            return "   6- ";
        }
        if (value.length > 15) {
            return "    15 ";
        }
        return true;
    }
};
export const nameValidation = {
    required: REQUIRED_FIELD,
    validate: (value: string) => {
        if (value.length > 15) {
            return "    15 ";
        }
        if (value.length < 3) {
            return "    3 ";
        }
        return true;
    }
};
